/* HEADINGS */
  
 .headline-text-primary, .text {
    color: #fff;
    text-align: center;
    line-height: 1.4;
  }

.headline .text {
    font-size: 1.5rem;
}
  
 .headline-text-primary {
    font-size: 2.5rem;
  }
  
  .features-header {
    color: #000;
    font-size: 1.1rem;
    text-align: center;
    line-height: 1.4;
    margin-bottom: 10px;
  }
  
  /* SECTIONS */
  
  .headline {
    width: 100%;
    height: 60vh;
    min-height: 350px;
    background: linear-gradient(rgba(0,0,0,0.3),rgba(0,0,0,0.7)), url('../../assets/images/ant.png');
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  
  .features {
    width: 100%;
    height: auto;
    background-color: #fff;
    display: flex;
    padding: 50px 20px;
    justify-content: space-around;
  }
  
  .feature-container {
    flex-basis: 30%;
    margin-top: 10px;
    background-color: #fff;
    border-radius: 7px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.12);
  }
  
  .feature-container .text {
    color: #000;
    padding: 10px;
    text-align: center;
    font-size: 16px;
    line-height: 1.4;
    margin-bottom: 15px;
  }
  
  .feature-container img {
    width: 100%;
    margin-bottom: 15px;
    border-radius: 7px 7px 0 0;
  }

  /* Media Queries */
  
    /* Mobile Devices - Phones/Tablets */
  
  @media only screen and (max-width: 720px) { 

    .headline .text {
        font-size: .9rem;
    }

    .features {
      flex-direction: column;
      padding: 50px;
    }
    
    /* MOBILE HEADINGS */
    
   .headline-text-primary {
      font-size: 1.9rem;
    }
    
    .text {
      font-size: 0.8rem;
    }

    .headline {
      height: 20vh;
    }
      
    .feature-container .text {
      margin-bottom: 25px;
    }
    
    .feature-container {
      margin-top: 20px;
    }
    
    .feature-container:nth-child(2) {
      order: -1;
    }
    
  }
